import { DEL_CITY } from "./actions";
import { defineState } from "redux-localstore";
import { getState } from "redux-localstore";

const defaultState = getState() || {
  cities: null
};

const initialState = defineState(defaultState)("city_reducer");

export function city_reducer(state = initialState, action) {
  switch (action.type) {
    case DEL_CITY:
      let cityToDelete = action.payload;
      let cityArrCopy = [state.cities];
      console.log(cityArrCopy);
      let newCitiesArr = cityArrCopy.filter(city => city !== cityToDelete);
      console.log(newCitiesArr);
      return {
        ...state,
        cities: newCitiesArr
      };
    default:
      return state;
  }
}



