import { ADD_CITY } from "./actions";
import { defineState } from "redux-localstore";
import { getState } from "redux-localstore";

const defaultState = getState() || {
  cities: null
};

const initialState = defineState(defaultState)("add_city_reducer");

export function add_city_reducer(state = initialState, action) {
  switch (action.type) {
    case ADD_CITY:
      let newCitiesArr = [action.payload];
      if (state.cities) {
        newCitiesArr = [...state.cities, action.payload];
      }
      return {
        ...state,
        cities: newCitiesArr
      };
    default:
      return state;
  }
}



